.chatScreen_text{
    margin-left: 10px;
    background-color: lightgray;
    padding: 15px;
    border-radius: 20px;
}
.chatScreen_textUser{
    margin-left: auto;
    background-color: #29b3cd;
    color: white;
    padding: 15px;
    border-radius: 20px;
}
.chatScreen_message{
    display: flex;
    align-items: center;
    padding: 10px;
}
.chatScreen_timestamp{
    text-align: center;
    padding: 20px;
    color: gray;
}
.chatScreen_input{
    display: flex;
    padding: 5px;
    position: fixed;
    bottom: 0;
    width: 100%;
    border-top: 1px solid lightgray;
}
.chatScreen_inputField{
    flex: 1;
    padding: 10px;
    border: none;
}
.chatScreen_inputButton{
    padding: 10px;
    border: none;
    background-color: #fff;
    color: #fe3d71;
    font-weight: bolder;
}